.app__newsletter {
    padding: 2rem 4rem;
    border: 1px solid var(--color-golden);
    background: var(--color-black) ;
}
.app__newsletter-heading {
    text-align: center;
}
.app__newsletter-input {
    flex-direction: row;
    margin-top: 3rem;
} 
.app__newsletter-input input {
    width: 620px;
    border: 1px solid var(--color-golden);
    border-radius: 5px;
    font-size: 1rem;
    font-family: var(--font-base);
    color: var(--color-white);
    margin-right: 2rem;
    padding: 0.75rem 1rem;
    background-color: var(--color-black);
}
.app__newsletter-input button {
    width: max-content;
}

@media screen and (min-width:2000px) {
    .app__newsletter-input input {
        font-size: 2rem;
    }
}
@media screen and (max-width:990px) {
    .app__newsletter-input {
        flex-direction: column;
        width: 100%;

    }
    .app__newsletter-input input {
        margin: 0 0 2rem 0 ;
        width: 100%;
    }
}
@media screen and (max-width: 650px) {
    .app__newsletter {
        padding: 2rem 0;
        border: none;
    }
}
@media screen and (max-width: 300px) {
    .app__newsletter-heading h1 {
        font-size: 32px;
        line-height: 50px;
    }
}

.app__footer-links {
    width: 100%;
    display: flex;
    justify-content: space-between;
    align-items: flex-start;
    margin-top: 5rem;
    padding: 0 2rem;
}
.app__footer-links_contact,
.app__footer-links_logo,
.app__footer-links_work {
    flex: 1;
    margin: 1rem;
    text-align: center;
}
.app__footer-links_logo img:first-child {
    width: 210px;
    margin-bottom: 0.75rem;
}
.app__footer-links_work p:nth-child(2n + 1) {
    margin-bottom: 1rem;
}
.app__footer-links-icons svg {
    color: var(--color-white);
    margin: 0.5rem;
    font-size: 24px;
    cursor: pointer;
}
.app__footer-links-icons svg:hover {
    color: var(--color-golden);
}
.app__footer-headtext {
    font-family: var(--font-base);
    color: var(--color-white);
    font-weight: 400;
    letter-spacing: 0.04em;
    text-transform: capitalize;
    font-size: 32px;
    line-height: 41.6px;
    margin-bottom: 1rem;
}

.footer__copyright {
    margin-top: 3rem;
}

@media screen and (min-width: 2000px) {
    .app__footer-headtext {
        font-size: 51px;
        line-height: 61px;
    }
}
@media screen and (max-width: 1150px) {
    .app__footer-links {
        align-items: center;
        flex-direction: column;
    }
    .app__footer-links_contact,
    .app__footer-links_logo,
    .app__footer-links_work {
        margin: 2rem 0 ;
        width: 100%;
    }
}
@media screen and (max-width: 650px) {
    .app__footer {
        padding: 0 0 2rem 0;
    }
}
@media screen and (max-width: 350px) {
    .app__footer-links_logo img:first-child {
        width: 80%;
    }
}